.section-about {
  background: var(--second-background);

  &__background {
    background-image: url('../images/mob/background-milk-mobile1x.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 93% 93%;
    @media screen and(min-width: 768px) and (max-width: 1279px) {
      background-position: -400% -400%;
    }
    @media screen and (min-width: 1280px) {
      background-position: 125% 125%;
    }
  }

  &__pre-title {
    font-family: Titan One;
    color: var(--second-title-color);
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
    text-align: center;
    text-transform: uppercase;
    padding-top: 125px;
    @media screen and (min-width: 768px) {
      font-size: 26px;
      line-height: 1.14;
      padding-top: 130px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 30px;
      line-height: 1.14;
      letter-spacing: 0.04rem;
      padding-top: 125px;
    }
  }
  &__title {
    font-family: Titan One;
    color: var(--main-title-color);
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.14;
    text-align: center;
    text-transform: uppercase;
    padding-top: 15px;
    padding-left: 40px;
    padding-right: 40px;
    letter-spacing: 0.04em;

    @media screen and (min-width: 768px) {
      font-size: 48px;
      line-height: 1.14;
      padding-top: 16px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 58px;
      line-height: 1.13;
      letter-spacing: 0.06rem;
      padding-top: 15px;
    }
  }
  &__picture {
    display: flex;
    justify-content: center;
    padding-top: 26px;
    @media screen and (min-width: 768px) {
      padding-top: 50px;
      margin-right: 17px;
    }
    @media screen and (min-width: 1280px) {
      padding-top: 55px;
      margin-right: 74px;
    }
  }
}
@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .section-about__background {
    background-image: url('../images/mob/background-milk-mobile2x.png');
  }
}
@media screen and (min-width: 1280px) {
  .section-about__background {
    background-image: url('../images/desk/background-milk-desktop1x.png');
  }

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .section-about__background {
      background-image: url('../images/desk/background-milk-desktop2x.png');
    }
  }
}

.section-about__position {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.section-about__text {
  @media screen and (max-width: 767px) {
    padding-top: 46px;
  }
  @media screen and (min-width: 768px) {
    width: 340px;
    padding-top: 100px;
  }
  @media screen and (min-width: 1280px) {
    width: 463px;
    padding-top: 106px;
  }
}
.section-about__list {
  display: flex;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.58;
  padding-bottom: 20px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 35px;
    line-height: 1.92;
  }

  color: var(--third-text-color);
  @media screen and (max-width: 767px) {
    padding-left: 30px;
    padding-right: 30px;
  }

  &--black {
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;
    padding-bottom: 45px;
    color: var(--second-text-color);
    @media screen and (min-width: 768px) {
      font-size: 14px;
      letter-spacing: 0.04em;
    }
    @media screen and (max-width: 767px) {
      padding-left: 30px;
      padding-right: 30px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
      letter-spacing: 0.04em;
      line-height: 1.75;
      padding-bottom: 20px;
    }
  }
  &--position {
    @media screen and (min-width: 768px) {
      padding-bottom: 62px;
    }
    @media screen and (min-width: 1280px) {
      padding-bottom: 45px;
    }
  }
}

.section-about__button {
  font-family: DM Sans;
  width: 176px;
  height: 44px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.28;
  display: flex;
  justify-content: center;
  align-items: center;

  background: var(--main-text-color);
  border-radius: 22px;
  color: var(--main-color-text-button);
  @media screen and (max-width: 767px) {
    margin-left: 30px;
  }
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.14;
  }
}

.section-about__icon {
  margin-left: 15px;
}
.section-about__button:hover,
.section-about__button:focus {
  background: #ecebeb;
}
